import React from "react";

export const Features = (props) => {
  return (
    <div id="features" className="text-center">
      <div className="col-md-10 col-md-offset-1 section-title">
      </div>
      <div className="container">
        <div className="row">
          {props.data
            ? props.data.map((d, i) => (
              <div key={`${d.title}-${i}`} className="col-xs-6 col-md-6 features-section">
                {" "}

                <h3> <i className={d.icon}></i>{d.title}</h3>
                <p>{d.text}</p>
              </div>
            ))
            : "Loading..."}
        </div>
      </div>
    </div>
  );
};
